import React, { useState, useEffect, Fragment } from "react";
import { BsArrowLeft } from "react-icons/bs";
import { history } from "helpers";
import "./style.scss";
import FormErrorMessage from "component/common/ErrorMessage";
import DropDown from "component/common/DropDown/DropDown";
import { useForm } from "react-hook-form";
import InputBox from "component/common/InputBox/InputBox";
import { editClient, updateClient } from "service/Auth";
import NormalButton from "component/common/NormalButton/NormalButton";
import CommonDatePicker from "component/common/CommonDatePicker/CommonDatePicker";
import CustomController from "component/common/Controller";
import { Toast } from "service/toast";

const EditClientsFamilyComp = () => {
  const {
    register,
    handleSubmit,
    errors,
    setValue,
    reset,
    setError,
    control,
    getValues,
  } = useForm({
    mode: "onChange",
  });
  const [modal, setModal] = useState(false);

  const [ClientDetails, setClientDetails] = useState({
    relationShip: "",
  });

  const options = [
    {
      label: "Software Enginerr",
      value: "Software enginerr",
    },
    {
      label: "Enginerr",
      value: "enginerr",
    },
    {
      label: "Software",
      value: "Software ",
    },
  ];
  const urlParams = new URLSearchParams(window.location.search);
  const id = urlParams.get("Editid");

  useEffect(() => {
    setValue(
      "relationShip",
      options.find((option) => option.value === ClientDetails.relationShip)
    );
  }, [ClientDetails, setValue]);

  const getClientDetails = async () => {
    try {
      const params = {
        id: id,
      };
      console.log("first");
      let response = await editClient(params);

      if (response.status === 200) {
        const data = response?.data.data;
        console.log('datadateOfBirth', data.dateOfBirth)
        reset({
          clientName: data?.clientName,
          email: data?.email,
          relativeName: data?.relativeName,
          dateOfBirth: new Date(data?.dateOfBirth),
        });
        setClientDetails({
          relationShip: data.relationShip,
        });
      } else {
        Toast({ type: "error", message: response.data.message });
      }
    } catch (e) {
      console.log("e :>> ", e);
    }
  };

  useEffect(() => {
    if (urlParams.has("Editid")) {
      // setEdit(true);

      getClientDetails();
    }
  }, []);
  const onSubmit = async (data) => {
    setModal(true);
    try {
      let body = {
        clientName: data?.clientName,
        email: data?.email,
        relativeName: data?.relativeName,
        dateOfBirth: data?.dateOfBirth,
        relationShip: ClientDetails.relationShip,
      };

      let response = await updateClient(body, id);
      if (response.status === 200) {
        setModal(true);
        const timeout = setTimeout(() => {
          setModal(false);
          reset(ClientDetails);
          history.push("/admin/clients-family");
        }, 1000);
        return () => clearTimeout(timeout);
      } else {
        Toast({ type: "error", message: response.data.message });
      }
    } catch (e) {
      console.log(e);
    }
  };

  return (
    <div className="px-5 py-3">
      <div className="edit_client d-flex my-3 align-items-center ">
        <i className="pr-3">
          <BsArrowLeft
            size={28}
            onClick={() => history.goBack()}
            style={{ cursor: "pointer" }}
          />
        </i>
        <p className="m-0">{"Client’s Family"}</p>
      </div>
      <form>
        <div className="client_box p-5">
          <div className="row ">
            <div className="col-md-4">
              <label>Client Name</label>
              <InputBox
                className="add_staff"
                type={"text"}
                placeholder="Enter Client Name"
                name="clientName"
                errors={errors}
                register={register({
                  required: true,
                })}
              />
              <FormErrorMessage
                error={errors.clientName}
                messages={{
                  required: "Client Name is required",
                }}
              />
            </div>
            <div className="col-md-4">
              <label>Email Id</label>
              <InputBox
                className="add_staff"
                type={"text"}
                placeholder="Enter Email Id"
                //   errors={errors}
                name="email"
                errors={errors}
                register={register({
                  required: true,
                  pattern: /\S+@\S+\.\S+/,
                })}
              />
              <FormErrorMessage
                error={errors.email}
                messages={{
                  required: "Email is required",
                  pattern: "Invalid Email",
                }}
              />
            </div>
            <div className="col-lg-2">
              <label>Date of Birth</label>
              <div className="date_of_birth">
                <CustomController
                  name={"dateOfBirth"}
                  control={control}
                  error={errors.dateOfBirth}
                  defaultValue={getValues("dateOfBirth" || "")}
                  rules={{ required: true }}
                  messages={{
                    required: "DOB is Required",
                  }}
                  render={({ onChange, ...field }) => {
                    return (
                      <CommonDatePicker
                        name="dateOfBirth"
                        {...field}
                        onChange={(date) => {
                          onChange(date);
                        }}
                        placeholder="DOB"
                      />
                    );
                  }}
                />
              </div>
            </div>
            <div className="col-md-4 my-3">
              <label>Relative Name</label>
              <InputBox
                className="add_staff"
                type={"text"}
                placeholder="Enter Relative Name"
                name="relativeName"
                errors={errors}
                register={register({
                  required: true,
                })}
              />
              <FormErrorMessage
                error={errors.relativeName}
                messages={{
                  required: "Relative Name is required",
                }}
              />
            </div>
            <div className="col-md-4 my-3">
              <label>Relationship</label>
              <CustomController
                name={"relationShip"}
                control={control}
                error={errors.relationShip}
                value={options.find(
                  (option) => option.value === getValues("relationShip")
                )}
                rules={{ required: true }}
                messages={{ required: "Select RelationShip is Required" }}
                render={({ onChange, ...field }) => {
                  return (
                    <DropDown
                      {...field}
                      name="relationShip"
                      error={errors.relationShip}
                      placeholder="Select RelationShip"
                      options={options}
                      onChange={(option) => {
                        setClientDetails((prevState) => ({
                          ...prevState,
                          relationShip: option.value,
                        }));
                      }}
                    />
                  );
                }}
              />
            </div>
          </div>
          <div className="d-flex align-items-center justify-content-end my-5">
            <div className="col-md-2">
              <NormalButton
                className="authButton1"
                label={"Cancel"}
                onClick={() => history.goBack()}
              />
            </div>
            <div className="col-md-2">
              <NormalButton
                className="loginButton"
                label={"Update"}
                onClick={handleSubmit(onSubmit)}
                //   onClick={DeletBulk}
              />
            </div>
          </div>
        </div>
      </form>
    </div>
  );
};

export default EditClientsFamilyComp;
